:root {
  --blue-color: #00acdc;
  --black: #000;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

header {
  background-color: #fff;
  justify-content: space-between;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.06px;
  display: flex;
}

ul {
  margin: 0;
  padding: 0;
}

.logo {
  padding: 10px 0;
}

.navigation ul {
  list-style-type: none;
  display: flex;
}

.navigation a {
  color: var(--black);
  text-transform: uppercase;
  justify-content: flex-end;
  margin-right: 20px;
  padding: 22.5px 0;
  text-decoration: none;
  display: flex;
}

.navigation a:hover {
  color: var(--blue-color);
}

.navigation li:last-child a {
  margin-right: 0;
}

.navigation a.is-active {
  color: var(--blue-color);
  padding-bottom: 23px;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
